<template>
	<view>
		<course-item :data='detailData' isRow></course-item>
		<divider></divider>
		<view class="">
			<!-- 优惠券 -->
			<view class="list-item">
				<view class="list-title">
					优惠券
				</view>
				<view class="">
					暂无可用
				</view>
				<uni-icons type="right" class="list-move" color="#cccccc"></uni-icons>
			</view>
			<view class="list-item">
				<view class="list-title">
					支付方式1
				</view>
				<view class="list-pay">
					微信支付
				</view>
				<uni-icons type="right" class="list-move" color="#cccccc"></uni-icons>
			</view>
			<!--  -->
		</view>
		<!-- 支付方式 -->
		<!-- 支付按钮 -->
		<view class="course-detail-buy">
			<button class="course-buy-btn" @click="onSubmit">
				立即支付 ¥{{ detailData.price }}
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: null,
				type: '',
				detailData:{
					cover: "http://demo-mp3.oss-cn-shenzhen.aliyuncs.com/egg-edu-demo/10ccf3a973f5193bec3c.png",
					id: null,
					price: "",
					title: "",
					type: "",
				}
			};
		},
		onLoad(e) {
			this.id = e.id
			this.type = e.type
			this.gegDetailData()
		},
		methods:{
			async gegDetailData(){
				const params = {}
				if(this.type === 'group'){
				
				}else{
					params.type = this.type
					params.id = this.id
					const { data } =await  this.$http.gegProdDetailApi(params)
					this.detailData = data
				}
			
			},
			/**
			 * 支付
			 */
			async onSubmit(){
				const params = {}
				params.goods_id = this.id
				params.type = this.type
				const {data}  = await this.$http.createdOrderApi(params)
				const no = data.no
				const { data:orderInfo }  = await this.$http.wxpayApi({ no, type:'app'})
			
		orderInfo.appId = orderInfo.appid
		console.log(orderInfo);
				uni.requestPayment({
				    provider: "wxpay", 
				    orderInfo,
				    success(res) {
							this.toast('1支付成功')
							},
				    fail(e) {
				       // 获取错误信息 err.errMsg
							 console.log(e.errMsg);
				    }
				})
			}
		}
	}
</script>

